.about-main {
    padding-top: 0px;
    background: linear-gradient(
      360deg,
      hsla(0, 0%, 100%, 1) 0%,
      rgb(230, 250, 255) 100%
    );
    /* padding-top: 25px; */
    /* padding-bottom: 50px; */
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .heading-container {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    padding-bottom: 30px;
  }
  .h2-font {
    font-size: 2rem;
    font-weight: 600;
    color: #002d4a;
    word-spacing: 5px;
  }
  .details{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .img{
    width: 35%;
    display: flex;
    flex-direction: row-reverse;
  }
  .img>img{
    height: 500px;
  }
  .data{
    width: 40%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    padding: 30px;
  }
  h3{
    color: #002d4a;
    /* margin: 0px; */
  }
  .data>p{
    color: #002d4a;
    margin: 0;
    text-align: justify;
  }
  .education{
    display: flex;
    margin: 15px 0;
    justify-content: center;
    flex-wrap: wrap;
    gap: 15px;
  }
  .edu{
    width: fit-content;
    width: 45%;
    padding: 15px;
    border: 1px solid rgba(0, 33, 94, 0.427);
    border-radius: 5px;
  }
  .heading-container{
    padding: 0%;
  }

  @media screen and (max-width: 782px){
    .details{
      flex-direction: row-reverse;
    }
    .img{
      width: 100%;
      justify-content: center;
    }
    .img>img{
      height: 300px;
    }
    .data{
      width: 80%;
      padding: 10px;
    }
    .edu{
      width: 100%;
    }
  }