.project-main {
  background: linear-gradient(
    360deg,
    hsla(0, 0%, 100%, 1) 0%,
    hsla(191, 100%, 97%, 1) 100%
  );
  margin-bottom: 50px;
}
.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px;
  margin-bottom: 50px;
}
.project-tiles {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 10px;
  width: 30%;
  padding: 0 0 20px 0;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px,
    rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px,
    rgba(0, 0, 0, 0.09) 0px 32px 16px;
  text-align: center;
  border-radius: 5px;
}
.project-tiles > img {
  width: 100%;
  border-radius: 5px 5px 0 0;
}
.project-tiles > h3 {
  font-size: 24px;
  margin: 0%;
  color: #002c4a;
}
.project-tiles > h3::after {
  content: "  ↗ ";
}
.project-tiles>p{
    
    padding: 0 40px;
}
.tab-btn{
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 30px;
  flex-wrap: wrap;
  /* margin-left: -40px; */
}
@media screen and (max-width: 782px){
  .project-tiles{
    width: 80%;
  }
}