body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
  /* background-color: rgba(241, 255, 250, 0.633); */
  font-family: poppins;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@keyframes image-animate {
  0% {
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%;
  }
  100% {
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  }
}

.header-main-container {
  margin-top: 50px;
  display: flex;
  padding: 50px;
  flex-wrap: wrap;
  justify-content: center;
  background: hsla(0, 0%, 100%, 1);

  background: linear-gradient(
    180deg,
    hsla(0, 0%, 100%, 1) 0%,
    rgb(230, 250, 255) 100%
  );
}
.iconlist {
  /* padding-top: 30px; */
  display: flex;
  flex-direction: columns;
  align-self: center;
  /* margin-left: 20px; */
}
.iconlist > ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.iconlist > ul > li {
  list-style: none !important;
  font-size: 23px;
  padding: 5px 10px 5px 10px;
}
.iconlist > ul > li > a {
  color: #002d4a;
  transition: 1s;
}
.iconlist > ul > li > a:hover {
  color: #37bce3;
  transition: 1s;
}
.intro-data {
  display: flex;
  flex-direction: column;
  align-self: center;
  margin-left: 50px;
  width: 30%;
  /* padding-top: 50px; */
}
.intro-data > h1 {
  font-size: 1.5rem;
  font-weight: 600;
  color: #00355b;
  word-spacing: 5px;
}
.intro-data > h2 {
  font-size: 3.5rem;
  font-weight: 600;
  color: #00355b;
  word-spacing: 5px;
}
.para-font {
  font-size: 1.1rem;
  padding: 10px;
  color: #005078;
  text-align: justify;
  font-family: sans-serif;
}
.btn1 {
  /* width: 30%; */
  margin: 10px 10px;
  padding: 7px 12px;
  font-size: 15px;
  font-family: sans-serif;
  background-color: #37bce3;
  border-color: #37bce3;
}
.btn1:hover {
  color: #37bce3;
  border-color: #37bce3;
  background-color: rgba(255, 255, 255, 0);
}

.display-img {
  display: flex;
  justify-content: center;
  width: 40%;
}
.display-img > img {
  animation: image-animate 6s ease-in-out 1s infinite;
  width: 400px;
  height: auto;
}

/* About Section Start */
.about-main {
  padding-top: 0px;
  background: linear-gradient(
    360deg,
    hsla(0, 0%, 100%, 1) 0%,
    rgb(230, 250, 255) 100%
  );
  /* padding-top: 25px; */
  /* padding-bottom: 50px; */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.heading-container {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  padding-bottom: 30px;
}
.h2-font {
  font-size: 2rem;
  font-weight: 600;
  color: #002d4a;
  word-spacing: 5px;
}
.custom-line {
  border: none;
  border-top: 3px solid darkblue; /* Set the line color to dark blue */
  /* border-radius: 1px; */
  margin: 10px 0; /* Add some margin for spacing */
  /* height: 10px;
  width: 20%; */
}
.para-one {
  width: 30%;
  padding: 50px 15px 0 15px;
  margin: 0 10px 0 10px;
}
.para-two {
  width: 30%;
  margin: 0 10px 0 10px;
  padding: 50px 15px 0 15px;
}

.dp-holder {
  background-color: #37bbe33f;
  margin-left: 40px;
  display: flex;
  align-items: center;
  border-radius: 20px;
}
.dp-holder > img {
  height: 480px;
  width: 360px;
  border-radius: 20px;
  margin-left: -20px;
  margin-right: 20px;
}

/* services section starts */
.service-container {
  background: linear-gradient(
    180deg,
    hsla(0, 0%, 100%, 1) 0%,
    hsla(191, 100%, 97%, 1) 100%
  );
  margin-top: 80px;
  padding-bottom: 80px;
}
.card-holder-services {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.value-cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: inherit;
  width: 23%;
  padding: 50px 30px;
  margin: 20px;
  border-radius: 15px;
  background-color: white !important;
  box-shadow: 0px 0px 30px #0d37542c;
}


.small-font {
  font-size: 1rem;
}
.h3-font {
  font-size: 1.5rem;
  font-weight: 500;
  color: #002d4a;
  word-spacing: 5px;
}
.value-cards > i {
  font-size: 3rem;
  margin-bottom: 20px;
  color: #002c4a;
}
.caraousel {
  width: 80%;
  height: 80vh;
}
.carousal-items {
  display: flex;
  padding-top: 30px;
  padding-bottom: 60px;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  background: linear-gradient(
    360deg,
    hsla(0, 0%, 100%, 1) 0%,
    hsla(191, 100%, 97%, 1) 100%
  );
}
.small-font1{
  font-size: 1.3rem;
}

.underText{
  font-size: 10px;
  text-align: center !important;
  color: #002c4a93;
}
.skill-cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: inherit;
  width: 23%;
  padding: 30px;
  margin: 20px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.1) !important;
  border: 1px solid rgba(0, 33, 94, 0.427) !important;
}
.skill-container{
  display: flex !important;
  flex-wrap: wrap;
  justify-content: space-between;
}
.skill{
  display: flex !important;
  flex-direction: column;
  align-items: center;
  width: 50%;
}
.skill-name{
  display: flex !important;
  align-items: center !important;
}
.skillName{
  font-size: 18px;
  margin-left: 4px;
}

/* Mobile view css */
@media screen and (max-width: 782px) {
  .header-main-container {
    padding: 15px;
    flex-direction: column-reverse;
    gap: 20px;
  }
  .display-img{
    width: 100%;
  }
  .display-img>img {
    width: 100%;
  }
  .intro-data {
    margin: 0;
    width: 100%;
    align-items: center;
  }
  .intro-data > h1,.intro-data > h2 {
    font-size: 2rem;
    text-align: center;
  }
  .para-font {
    font-size: 1rem;
  }
  .btn1 {
    /* width: 50%; */
    margin: 5px 5px;
    padding-top: 5px;
    padding-bottom: 5px;
    font-size: 16px;
  }
  .iconlist > ul {
    flex-direction: row;
    padding: 0px 0;
    justify-content: space-around;
  }
  .about-main {
    padding: 15px;
  }
  .h2-font {
    font-size: 1.8rem;
    text-align: center;
  }
  .heading-container {
    padding: 0;
  }
  .para-one,
  .para-two {
    width: 100%;
    padding: 15px;
  }
  .dp-holder {
    padding: 20px 20px;
    margin-left: 40px;
  }
  .dp-holder > img {
    height: 400px;
    width: 300px;
    margin-left: -60px;
    margin-right: 10px;
  }
  .h3-font {
    font-size: 1.4rem;
  }
  .service-container {
    margin-top: 30px;
    padding: 15px;
  }
  .value-cards {
    width: 100%;
    height: inherit !important;
  }
  .value-cards>h3, .value-cards>p{
    text-align: center;
    }
  .caraousel {
    width: 90%;
    height: inherit !important;
  }
  .caraousel-items {
    padding-bottom: 30px;
  }
  .skill-cards{
    width: 100%;
  }
}
