.footer-container {
  display: flex;
  justify-content: center;
  padding: 50px 0;
}
.footer-card {
  padding: 25px;
  width: 60%;
  box-shadow: rgba(3, 2, 101, 0.16) 0px 3px 6px, rgba(7, 2, 101, 0.23) 0px 3px 6px;
  border-radius: 5px;
}
.footer-card > h2 {
  color: #002d4a;
}
.footer-list > ul {
  display: flex;
  flex-direction: row;
  /* justify-content: center; */
  padding: 0%;
  align-items: center;
}
.footer-list > ul > li {
  list-style: none !important;
  font-size: 25px;
  padding: 5px 20px 5px 10px;
}
.footer-list > ul > li > a {
  color: #002d4a;
  transition: 1s;
}
.footer-list > ul > li > a:hover {
  color: #37bce3;
  transition: 1s;
}
@media screen and (max-width: 782px){
  .footer-card{
    width: 80%;
  }
}